<template>
  <div>
    <!--  -->
    <el-breadcrumb separator="/">
      <el-breadcrumb-item :to="{ path: '/home' }"></el-breadcrumb-item>
      <el-breadcrumb-item></el-breadcrumb-item>
      <el-breadcrumb-item></el-breadcrumb-item>
    </el-breadcrumb>
    <!--  -->
    <el-card>
      <el-row>
        <el-col :span="10">
          <div class="grid-content bg-purple-dark">
            <el-input placeholder="" v-model="userName" @change="searchUserHoliday()">
              <el-button slot="append" icon="el-icon-search" @click="searchUserHoliday()"></el-button>
            </el-input>
          </div>
        </el-col>
      </el-row>
      <el-table :data="userHolidayList" border style="width: 100%">
        <el-table-column
          prop="user"
          label=""
          width="180">
        </el-table-column>
        <el-table-column
          prop="apply_date"
          width="180"
          label="">
        </el-table-column>
        <el-table-column
          prop="bdate"
          label=""
          width="180">
        </el-table-column>
        <el-table-column
          prop="edate"
          width="180"
          label="">
        </el-table-column>
        <el-table-column
          prop="notes"
          width="180"
          label="">
        </el-table-column>
        <el-table-column
          prop="date_num"
          width="100"
          label="">
        </el-table-column>
        <el-table-column
          prop=""
          label="">
          <template slot-scope="scope">
            <el-button type="primary" @click="showModifyDialog(scope.row)" size="mini"></el-button>
          </template>
        </el-table-column>
      </el-table>
      <el-pagination
        @size-change="handleSizeChange"
        @current-change="handleCurrentChange"
        :page-sizes="page_sizes"
        :page-size="page_size"
        layout="total, sizes, prev, pager, next"
        :total="total">
      </el-pagination>
    </el-card>
    <!--  -->
    <el-dialog
      title="" :visible.sync="modifyHolidayDialogVisable" width="35%" @close="resetModifyDialog">
      <el-form ref="modifyFormRef" :model="modifyHolidayForm" label-width="110px" :rules="modifyHolidayFormRules">
        <el-form-item label="" >
          <el-input v-model="modifyHolidayForm.user" disabled></el-input>
        </el-form-item>
        <el-form-item label="" prop="dateRange">
          <el-date-picker
            v-model="modifyHolidayForm.dateRange"
            type="daterange"
            range-separator=""
            start-placeholder=""
            end-placeholder=""
            :picker-options="datePickerOptions">
          </el-date-picker>
        </el-form-item>
        <!--        <el-form-item label="" prop="bdate">-->
        <!--          <el-date-picker value-format="yyyy-MM-dd"-->
        <!--                          v-model="modifyHolidayForm.bdate"  type="date" placeholder="">-->
        <!--          </el-date-picker>-->
        <!--        </el-form-item>-->
        <!--        <el-form-item label="" prop="edate">-->
        <!--          <el-date-picker value-format="yyyy-MM-dd"-->
        <!--                          v-model="modifyHolidayForm.edate"  type="date" placeholder="">-->
        <!--          </el-date-picker>-->
        <!--        </el-form-item>-->
        <el-form-item label="" prop="notes">
          <el-input v-model="modifyHolidayForm.notes"></el-input>
        </el-form-item>
        <!--<el-form-item label="" prop="apply_date">
          <el-date-picker value-format="yyyy-MM-dd"
                          v-model="modifyHolidayForm.apply_date"  type="date" placeholder="">
          </el-date-picker>
        </el-form-item>-->
        <el-form-item >
              <span class="button_span">
                <el-button type="primary" @click="modifyHoliday()" size="small"></el-button>
                <el-button type="info" @click="resetModifyDialog ()" size="small"></el-button>
              </span>
        </el-form-item>
      </el-form>
    </el-dialog>
  </div>
</template>
<script>
export default {
  data () {
    return {
      userName: '',
      userHolidayList: [],
      page_sizes: [1, 2, 5, 10],
      page_size: 5,
      current_page: 1,
      total: 0,
      datePickerOptions: {
        disabledDate: (time) => {
          const nowDate = new Date()
          const oneDay = 1000 * 60 * 60 * 24
          const oneYearLater = new Date(nowDate.getTime() + (oneDay * 365))
          return time.getTime() < nowDate || time.getTime() > oneYearLater
        }
      },
      modifyHolidayDialogVisable: false,
      modifyHolidayForm: {
        user: '',
        bdate: '',
        edate: '',
        apply_date: '',
        date_num: '',
        notes: '',
        dateRange: ''
      },
      modifyHolidayFormRules: {
        dateRange: [{ required: true, message: '', trigger: 'blur' }],
        notes: [{ required: true, message: '', trigger: true }]
      }
    }
  },
  created () {
    this.searchUserHoliday()
  },
  methods: {
    // 
    searchUserHoliday () {
      const currentIndex = (this.current_page - 1) * this.page_size
      this.$http.get('/admin/get_all_holiday?username=' + this.userName + '&current_index=' + currentIndex + '&page_size=' + this.page_size)
        .then(res => {
          if (res.data.state === 200) {
            this.total = res.data.total
            this.userHolidayList = res.data.data
          } else {
            this.$message.error('')
          }
        })
        .catch(_error => {
          this.$message.error('')
        })
    },
    // page_size
    handleSizeChange (pageSize) {
      this.page_size = pageSize
      this.searchUserHoliday()
    },
    // current_page
    handleCurrentChange (currentPage) {
      this.current_page = currentPage
      this.searchUserHoliday()
    },
    // 
    showModifyDialog (row) {
      this.modifyHolidayDialogVisable = true
      this.modifyHolidayForm = row
      console.log(row)
    },
    // 
    resetModifyDialog () {
      this.$refs.modifyFormRef.resetFields()
    },
    // 
    modifyHoliday () {
      this.modifyHolidayForm.bdate = this.modifyHolidayForm.dateRange[0]
      this.modifyHolidayForm.edate = this.modifyHolidayForm.dateRange[1]
      console.log(this.modifyHolidayForm.bdate)
      console.log(this.modifyHolidayForm.edate)
      this.$http.post('/admin/modify_holiday', this.modifyHolidayForm)
        .then(res => {
          if (res.data.state === 200) {
            this.modifyHolidayDialogVisable = false
            this.searchUserHoliday()
            this.$message.success('')
          } else {
            this.$message.error('')
          }
        })
        .catch(_error => {
          this.$message.error('')
        })
    }
  }
}
</script>
<style scoped>
.el-table{
  margin-top: 10px;
}
.el-pagination{
  margin-top: 10px;
}
.addBtn{
  margin-left: 10px;
