<template>
    <div>
        <!--  -->
        <el-breadcrumb separator="/">
            <el-breadcrumb-item :to="{ path: '/home' }"></el-breadcrumb-item>
            <el-breadcrumb-item></el-breadcrumb-item>
            <el-breadcrumb-item></el-breadcrumb-item>
        </el-breadcrumb>
        <!--  -->
        <el-card>
            <el-row>
              <el-col :span="10">
                <div class="grid-content bg-purple-dark">
                <el-input placeholder="" v-model="salaryName" @change="searchSalary()">
                  <el-button slot="append" icon="el-icon-search" @click="searchSalary()"></el-button>
                </el-input>
                </div>
              </el-col>
              <el-col :span="5" class="addBtn">
                <el-button type="primary" @click="showAddSalaryDialog()"></el-button>
              </el-col>
            </el-row>
            <el-table :data="salaryList" border style="width: 100%">
              <el-table-column
                prop="notes"
                label=""
                width="300">
              </el-table-column>
              <el-table-column
                prop="salary"
                label=""
                width="280">
              </el-table-column>
              <el-table-column
                prop=""
                label="">
                <template slot-scope="scope">
                  <el-button type="danger" @click="deleteSalary(scope.row)" size="mini"></el-button>
                  <el-button type="success" @click="showModifyDialog(scope.row)" size="mini"></el-button>
                </template>
              </el-table-column>
            </el-table>
            <el-pagination
              @size-change="handleSizeChange"
              @current-change="handleCurrentChange"
              :page-sizes="page_sizes"
              :page-size="page_size"
              layout="total, sizes, prev, pager, next"
              :total="SalaryListNum">
            </el-pagination>
        </el-card>
         <!-- -->
        <el-dialog
          title="" :visible.sync="addSalaryDialogVisable" width="35%" @close="resetDialog">
          <el-form ref="addFormRef" :model="addSalaryForm" label-width="110px" :rules="addSalaryFormRules">
            <el-form-item label="" prop="notes">
              <el-input v-model="addSalaryForm.notes"></el-input>
            </el-form-item>
            <el-form-item label="" prop="salary">
              <el-input v-model="addSalaryForm.salary"></el-input>
            </el-form-item>
            <el-form-item >
              <span class="button_span">
                <el-button type="primary" @click="addSalary()" size="small"></el-button>
                <el-button type="info" @click="resetDialog ()" size="small"></el-button>
              </span>
            </el-form-item>
        </el-form>
        </el-dialog>
         <!-- -->
        <el-dialog
          title="" :visible.sync="modifySalaryDialogVisable" width="35%" @close="resetModifyDialog">
          <el-form ref="modifyFormRef" :model="modifySalaryForm" label-width="110px" :rules="modifySalaryFormRules">
            <el-form-item label="" prop="notes">
              <el-input v-model="modifySalaryForm.notes"></el-input>
            </el-form-item>
            <el-form-item label="" prop="salary">
              <el-input v-model="modifySalaryForm.salary"></el-input>
            </el-form-item>
            <el-form-item >
              <span class="button_span">
                <el-button type="primary" @click="modifySalary()" size="small"></el-button>
                <el-button type="info" @click="resetModifyDialog ()" size="small"></el-button>
              </span>
            </el-form-item>
        </el-form>
        </el-dialog>
    </div>
</template>
<script>
export default {
  data () {
    return {
      salaryName: '',
      salaryList: [],
      page_sizes: [1, 2, 5, 10],
      page_size: 5,
      current_page: 1,
      SalaryListNum: 0,
      addSalaryDialogVisable: false,
      addSalaryForm: {
        notes: '',
        salary: 0
      },
      addSalaryFormRules: {
        notes: [{ required: true, message: '', trigger: 'blur' }],
        salary: [{ required: true, message: '', trigger: 'blur' }]
      },
      modifySalaryDialogVisable: false,
      modifySalaryForm: {
        notes: '',
        salary: 0
      },
      modifySalaryFormRules: {
        notes: [{ required: true, message: '', trigger: 'blur' }],
        salary: [{ required: true, message: '', trigger: 'blur' }]
      }
    }
  },
  created () {
    this.searchSalary()
  },
  methods: {
    // 
    searchSalary () {
      const currentIndex = (this.current_page - 1) * this.page_size
      this.$http.get('/admin/get_all_salary?notes=' + this.salaryName + '&current_index=' + currentIndex + '&page_size=' + this.page_size)
        .then(res => {
          if (res.data.state === 200) {
            console.log(res.data)
            this.salaryList = res.data.data
            this.SalaryListNum = res.data.total
          } else {
            this.$message.error('')
          }
        })
        .catch(_error => {
          this.$message.error('' + _error)
        })
    },
    // page_size
    handleSizeChange (pageSize) {
      this.page_size = pageSize
      this.searchSalary()
    },
    // page
    handleCurrentChange (currentPage) {
      this.current_page = currentPage
      this.searchSalary()
    },
    // 
    deleteSalary (row) {
      // 
      this.$confirm('')
        .then(res => {
          this.$http.delete('/admin/delete_salary?id=' + row.id)
            .then(res => {
              if (res.data.state === 200) {
                this.searchSalary()
                this.$message.success('')
              } else {
                this.$http.error('')
              }
            })
            .catch(_error => {
              this.$http.error('')
            })
        })
        .catch(_error => {
          this.$message.info('')
        })
    },
    // 
    showModifyDialog (row) {
      this.modifySalaryDialogVisable = true
      this.modifySalaryForm = row
    },
    // 
    showAddSalaryDialog () {
      this.addSalaryDialogVisable = true
    },
    // 
    resetDialog () {
      this.$refs.addFormRef.resetFields()
    },
    // 
    addSalary () {
      // 
      this.$refs.addFormRef.validate(valid => {
        if (valid === true) {
          // 
          this.$http.post('/admin/create_salary', this.addSalaryForm)
            .then(res => {
              if (res.data.state === 200) {
                this.addSalaryDialogVisable = false
                this.searchSalary()
                this.$message.success('')
              } else {
                this.$message.error('')
              }
            })
            .catch(_error => {
              this.$message.error('')
            })
        }
      })
    },
    // 
    resetModifyDialog () {
      this.$refs.modifyFormRef.resetFields()
    },
    // 
    modifySalary () {
      // 
      this.$refs.modifyFormRef.validate(valid => {
        if (valid === true) {
          // 
          this.$http.put('/admin/modify_salary', this.modifySalaryForm)
            .then(res => {
              if (res.data.state === 200) {
                this.modifySalaryDialogVisable = false
                this.searchSalary()
                this.$message.success('')
              } else {
                this.$message.error('')
              }
            })
            .catch(_error => {
              this.$message.error('')
            })
        }
      })
    }
  }
}
</script>
<style lang="less" scoped>
.el-table{
  margin-top: 10px;
}
.el-pagination{
  margin-top: 10px;
}
.addBtn{
  margin-left: 10px;
}
</style>
