<template>
    <div>
        <!--  -->
        <el-breadcrumb separator="/">
            <el-breadcrumb-item :to="{ path: '/home' }"></el-breadcrumb-item>
            <el-breadcrumb-item></el-breadcrumb-item>
            <el-breadcrumb-item></el-breadcrumb-item>
        </el-breadcrumb>
        <el-card>
            <el-row>
              <el-col :span="10">
                <div class="grid-content bg-purple-dark">
                <el-col :span="5" class="addBtn">
                    <el-button type="info" @click="searchCertFlow()"></el-button>
                </el-col>
                </div>
              </el-col>
            </el-row>
            <el-table :data="certFlowList" border style="width: 100%">
              <el-table-column
                prop="user_name"
                label=""
                width="100">
              </el-table-column>
              <el-table-column
                prop="apply_date"
                label=""
                width="100">
              </el-table-column>
              <el-table-column
                prop="type"
                label=""
                width="100">
                <template slot-scope="scope">
                    <el-select v-model="scope.row.type" placeholder="" disabled>
                        <el-option
                        v-for="item in options"
                        :key="item.value"
                        :label="item.label"
                        :value="item.value">
                        </el-option>
                    </el-select>
                </template>
              </el-table-column>
              <el-table-column
                prop="cert_no"
                label="">
              </el-table-column>
              <el-table-column
                prop="cert_name"
                label="">
              </el-table-column>
              <el-table-column
                prop="cert_date"
                label="">
              </el-table-column>
              <el-table-column
                prop=""
                label="" width="180">
                <template slot-scope="scope">
                  <el-button type="success" @click="agree(scope.row)" size="mini"></el-button>
                  <el-button type="info" @click="disagree(scope.row)" size="mini"></el-button>
                </template>
              </el-table-column>
            </el-table>
        </el-card>
    </div>
</template>
<script>
export default {
  data () {
    return {
      certFlowList: [],
      value: 0,
      options: [
        {
          value: 0,
          label: ''
        },
        {
          value: 1,
          label: ''
        }
      ]
    }
  },
  created () {
    this.searchCertFlow()
  },
  methods: {
    // 
    searchCertFlow () {
      this.$http.get('/admin/get_proving_certflow?usernaem=&cert_name=&current_index=0&page_size=100')
        .then(res => {
          if (res.data.state === 200) {
            console.log(res.data.data)
            this.certFlowList = res.data.data
          } else {
            this.$message.error('')
          }
        })
        .catch(_error => {
          this.$message.error('')
        })
    },
    // 
    agree (row) {
      this.$http.put('/admin/deal_cert_flow?id=' + row.id + '&type=0')
        .then(res => {
          if (res.data.state === 200) {
            this.searchCertFlow()
            this.$message.success('')
          } else {
            this.$message.error('')
          }
        })
        .catch(_error => {
          this.$message.error('' + _error)
        })
    },
    // 
    disagree (row) {
      this.$http.put('/admin/deal_cert_flow?id=' + row.id + '&type=1')
        .then(res => {
          if (res.data.state === 200) {
            this.searchCertFlow()
            this.$message.success('')
          } else {
            this.$message.error('')
          }
        })
        .catch(_error => {
          this.$message.error('' + _error)
        })
    }
  }
}
</script>
<style lang="less" scoped>
.el-table{
  margin-top: 10px;
}
.el-pagination{
  margin-top: 10px;
}
.addBtn{
  margin-left: 10px;
}
</style>
