<template>
    <div>
        <!--  -->
        <el-breadcrumb separator="/">
            <el-breadcrumb-item :to="{ path: '/home' }"></el-breadcrumb-item>
            <el-breadcrumb-item></el-breadcrumb-item>
            <el-breadcrumb-item></el-breadcrumb-item>
        </el-breadcrumb>
        <el-row>
              <el-col :span="10">
                <el-form ref="changePasswordFormRef" :model="changePasswordForm" label-width="110px" :rules="changePasswordFormRules">
                    <el-form-item label="" prop="oldPassword">
                        <el-input v-model="changePasswordForm.oldPassword" type="password"></el-input>
                    </el-form-item>
                    <el-form-item label="" prop="newPassword">
                        <el-input v-model="changePasswordForm.newPassword" type="password"></el-input>
                    </el-form-item>
                    <el-form-item label="" prop="repeadNewPassword">
                        <el-input v-model="changePasswordForm.repeadNewPassword" type="password"></el-input>
                    </el-form-item>
                    <el-form-item >
                    <span class="button_span">
                        <el-button type="primary" @click="changePassword()" size="small"></el-button>
                    </span>
                    </el-form-item>
                </el-form>
              </el-col>
        </el-row>
    </div>
</template>
<script>
export default {
  data () {
    return {
      changePasswordForm: {
        oldPassword: '',
        newPassword: '',
        repeadNewPassword: ''
      },
      changePasswordFormRules: {
        oldPassword: [{ required: true, message: '', trigger: 'blur' }],
        newPassword: [{ required: true, message: '', trigger: 'blur' }],
        repeadNewPassword: [{ required: true, message: '', trigger: 'blur' }]
      }
    }
  },
  created () {

  },
  methods: {
    // 
    changePassword () {
      // 
      if (this.changePasswordForm.newPassword === this.changePasswordForm.repeadNewPassword) {
        // 
        this.$refs.changePasswordFormRef.validate(valid => {
          if (valid === true) {
            // )
            this.$http.put('/user/modify_user_password?oldPassword=' + this.changePasswordForm.oldPassword + '&newPassword=' + this.changePasswordForm.newPassword)
              .then(res => {
                if (res.data.state === 200) {
                  sessionStorage.clear()
                  this.$router.replace('/login')
                  this.$message.success('')
                } else {
                  this.$message.error('')
                }
              })
          }
        })
      } else {
        this.$message.error('')
      }
    }
  }
}
</script>
