<template>
    <div>
        <!--  -->
        <el-breadcrumb separator="/">
            <el-breadcrumb-item :to="{ path: '/home' }"></el-breadcrumb-item>
            <el-breadcrumb-item></el-breadcrumb-item>
            <el-breadcrumb-item></el-breadcrumb-item>
        </el-breadcrumb>

        <!--  -->
        <el-card>
            <el-row>
              <el-col :span="10">
                <div class="grid-content bg-purple-dark">
                <el-input placeholder="" v-model="unitName" @change="searchUnit()">
                  <el-button slot="append" icon="el-icon-search" @click="searchUnit()"></el-button>
                </el-input>
                </div>
              </el-col>
              <el-col :span="5" class="addBtn">
                <el-button type="primary" @click="addUnitDialogVisable = true"></el-button>
              </el-col>
            </el-row>
            <el-table :data="unitList" border style="width: 100%">
              <el-table-column
                prop="name"
                label=""
                width="180">
              </el-table-column>
              <el-table-column
                prop="address"
                label=""
                width="180">
              </el-table-column>
              <el-table-column
                prop="phone"
                label="">
              </el-table-column>
              <el-table-column
                prop="manager"
                label="">
              </el-table-column>
              <el-table-column
                prop=""
                label="">
                <template slot-scope="scope">
                  <el-button type="danger" @click="deleteUnit(scope.row)" size="mini"></el-button>
                  <el-button type="success" @click="modifyUnit(scope.row)" size="mini"></el-button>
                </template>
              </el-table-column>
            </el-table>
            <el-pagination
              @size-change="handleSizeChange"
              @current-change="handleCurrentChange"
              :page-sizes="page_sizes"
              :page-size="page_size"
              layout="total, sizes, prev, pager, next"
              :total="unitListNum">
            </el-pagination>
        </el-card>
        <el-dialog
          title="" :visible.sync="addUnitDialogVisable" width="35%" @close="resetDialog">
          <el-form ref="addFormRef" :model="addUnitForm" label-width="110px" :rules="addUnitFormRules">
            <el-form-item label="" prop="name">
              <el-input v-model="addUnitForm.name"></el-input>
            </el-form-item>
            <el-form-item label="" prop="address">
              <el-input v-model="addUnitForm.address"></el-input>
            </el-form-item>
            <el-form-item label="" prop="phone">
              <el-input v-model="addUnitForm.phone"></el-input>
            </el-form-item>
            <el-form-item label="" prop="manager">
              <el-input v-model="addUnitForm.manager"></el-input>
            </el-form-item>
            <el-form-item >
              <span class="button_span">
                <el-button type="primary" @click="addUnit"></el-button>
                <el-button type="info" @click="resetDialog"></el-button>
              </span>
            </el-form-item>
        </el-form>
        </el-dialog>
        <el-dialog
          title="" :visible.sync="modifyUnitDialogVisable" width="35%" @close="resetModifiDialog">
          <el-form ref="modifyFormRef" :model="modifyUnitForm" label-width="110px" :rules="modifyUnitFormRules">
            <el-form-item label="" prop="name">
              <el-input v-model="modifyUnitForm.name"></el-input>
            </el-form-item>
            <el-form-item label="" prop="address">
              <el-input v-model="modifyUnitForm.address"></el-input>
            </el-form-item>
            <el-form-item label="" prop="phone">
              <el-input v-model="modifyUnitForm.phone"></el-input>
            </el-form-item>
            <el-form-item label="" prop="manager">
              <el-input v-model="modifyUnitForm.manager"></el-input>
            </el-form-item>
            <el-form-item >
              <span class="button_span">
                <el-button type="primary" @click="modifyUnit2" size="small"></el-button>
                <el-button type="info" @click="resetDialog" size="small"></el-button>
              </span>
            </el-form-item>
        </el-form>
        </el-dialog>
    </div>
</template>
<script>
export default {
  data () {
    return {
      unitName: '',
      unitList: [],
      unitListNum: 0,
      page_sizes: [1, 2, 5, 10],
      page_size: 5,
      current_page: 1,
      addUnitDialogVisable: false,
      addUnitForm: {
        name: '',
        address: '',
        phone: '',
        manager: ''
      },
      addUnitFormRules: {
        name: [
          { required: true, message: '', trigger: 'blur' }
        ],
        address: [
          { required: true, message: '', trigger: 'blur' }
        ],
        phone: [
          { required: true, message: '', trigger: 'blur' }
        ],
        manager: [
          { required: true, message: '', trigger: 'blur' }
        ]
      },
      modifyUnitDialogVisable: false,
      modifyUnitForm: {
        id: '',
        name: '',
        address: '',
        phone: '',
        manager: ''
      },
      modifyUnitFormRules: {
        name: [
          { required: true, message: '', trigger: 'blur' }
        ],
        address: [
          { required: true, message: '', trigger: 'blur' }
        ],
        phone: [
          { required: true, message: '', trigger: 'blur' }
        ],
        manager: [
          { required: true, message: '', trigger: 'blur' }
        ]
      }
    }
  },
  created () {
    this.searchUnit()
  },
  methods: {
    searchUnit () {
      const currentIndex = (this.current_page - 1) * this.page_size
      this.$http.get('/user/getunitbyname?name=' + this.unitName + '&current_index=' + currentIndex + '&page_size=' + this.page_size)
        .then(res => {
          console.log(res)
          if (res.data.state === 200) {
            this.unitList = res.data.data
            this.unitListNum = res.data.allnum
            // this.$message.success('')
          } else {
            this.$message.error(res.data.msg)
          }
        })
        .catch(error => {
          console.log(error)
          sessionStorage.clear()
          this.$router.push('/login')
          this.$message.error('')
        })
    },
    // 
    handleSizeChange (pageSize) {
      this.page_size = pageSize
      this.searchUnit()
    },

    // 
    handleCurrentChange (currentPage) {
      this.current_page = currentPage
      this.searchUnit()
    },
    // 
    resetDialog () {
      this.$refs.addFormRef.resetFields()
    },
    // 
    addUnit () {
      // 
      this.$refs.addFormRef.validate(valid => {
        if (valid === true) {
          // 
          this.$http.post('/admin/create_unit', this.addUnitForm)
            .then(res => {
              console.log(res)
              if (res.data.state === 200) {
                this.$message.success('')
                this.addUnitDialogVisable = false
                this.searchUnit()
              } else {
                this.$message.error(res.data.msg)
              }
            })
            .catch(error => {
              this.$message.error(error)
            })
        }
      })
    },
    // 
    deleteUnit (row) {
      // console.log(row)
      this.$confirm('')
        .then(async => {
        // 
          this.$http.delete('/admin/deleteunit?id=' + row.id)
            .then(res => {
              if (res.data.state === 200) {
                this.$message.success('')
                this.searchUnit()
              } else {
                this.$message.error('')
              }
            })
        })
    },
    // 
    modifyUnit (row) {
      this.modifyUnitDialogVisable = true
      this.modifyUnitForm = row
    },
    // 
    modifyUnit2 () {
      this.$refs.modifyFormRef.validate(valid => {
        if (valid === true) {
          // 
          this.$http.put('/admin/modify_unit', this.modifyUnitForm)
            .then(res => {
              if (res.data.state === 200) {
                this.modifyUnitDialogVisable = false
                this.searchUnit()
                this.$message.success('')
              } else {
                this.$message.error(res.data.msg)
              }
            })
        }
      })
    },
    // 
    resetModifiDialog () {
      this.$refs.modifyFormRef.resetFields()
    }
  }
}
</script>
<style lang="less" scoped>
.el-table{
  margin-top: 10px;
}
.el-pagination{
  margin-top: 10px;
}
.addBtn{
  margin-left: 10px;
}
</style>
