<template>
  <div>
    <!--  -->
    <el-breadcrumb separator="/">
      <el-breadcrumb-item :to="{ path: '/home' }"></el-breadcrumb-item>
      <el-breadcrumb-item></el-breadcrumb-item>
      <el-breadcrumb-item></el-breadcrumb-item>
    </el-breadcrumb>
    <el-card>
      <el-row>
        <el-col :span="10">
          <div class="grid-content bg-purple-dark">
            <el-col :span="10" class="addBtn">
              <el-button type="primary" @click="showAddCertDialog()"></el-button>
            </el-col>
            <el-col :span="5" class="addBtn">
              <el-button type="info" @click="searchCert()"></el-button>
            </el-col>
          </div>
        </el-col>
      </el-row>
      <el-table :data="certList" border style="width: 100%">
        <el-table-column
          prop="cert_no"
          label=""
          width="200">
        </el-table-column>
        <el-table-column
          prop="cert_name"
          label=""
          width="280">
        </el-table-column>
        <el-table-column
          prop="cert_date"
          label="">
        </el-table-column>
        <el-table-column
          prop=""
          label="" width="180">
          <template slot-scope="scope">
            <el-button type="success" @click="showModifyDialog(scope.row)" size="mini"></el-button>
          </template>
        </el-table-column>
      </el-table>
    </el-card>
    <!--  -->
    <el-dialog
      title="" :visible.sync="addCertDialogVisable" width="35%" @close="resetDialog">
      <el-date-picker v-model="value7" type="daterange" align="right" unlink-panels range-separator="" start-placeholder=""
       end-placeholder=""
      :picker-options="pickerOptions2">
    </el-date-picker>
        <el-form-item >
              <span class="button_span">
                <el-button type="primary" @click="addCert()" size="small"></el-button>
                <el-button type="info" @click="resetDialog ()" size="small"></el-button>
              </span>
        </el-form-item>
    </el-dialog>
    <!--  -->
    <el-dialog
      title="" :visible.sync="modifyCertDialogVisable" width="35%" @close="resetModifyDialog">
      <el-form ref="modifyFormRef" :model="modifyCertForm" label-width="110px" :rules="modifyCertFormRules">
        <el-form-item label="" prop="cert_no">
          <el-input v-model="modifyCertForm.cert_no"></el-input>
        </el-form-item>
        <el-form-item label="" prop="cert_name">
          <el-input v-model="modifyCertForm.cert_name"></el-input>
        </el-form-item>
        <el-form-item label="" prop="cert_name">
          <el-date-picker value-format="yyyy-MM-dd"
                          v-model="modifyCertForm.cert_date"  type="date" placeholder="">
          </el-date-picker>
        </el-form-item>
        <el-form-item >
              <span class="button_span">
                <el-button type="primary" @click="modifyCert()" size="small"></el-button>
                <el-button type="info" @click="resetModifyDialog ()" size="small"></el-button>
              </span>
        </el-form-item>
      </el-form>
    </el-dialog>
  </div>
</template>
<script>
export default {
  data () {
    return {
      certList: [],
      addCertDialogVisable: false,
      addCertForm: {
        cert_no: '',
        cert_name: '',
        cert_date: ''
      },
      addCertFormRules: {
        cert_no: [{ required: true, message: '', trigger: 'blur' }],
        cert_name: [{ required: true, message: '', trigger: 'blur' }],
        cert_date: [{ required: true, message: '', trigger: 'blur' }]
      },
      modifyCertDialogVisable: false,
      modifyCertForm: {
        pre_id: '',
        cert_no: '',
        cert_name: '',
        cert_date: ''
      },
      modifyCertFormRules: {
        cert_no: [{ required: true, message: '', trigger: 'blur' }],
        cert_name: [{ required: true, message: '', trigger: 'blur' }],
        cert_date: [{ required: true, message: '', trigger: 'blur' }]
      }
    }
  },
  created () {
    this.searchCert()
  },
  methods: {
    // 
    searchCert () {
      this.$http.get('/user/get_user_cert')
        .then(res => {
          if (res.data.state === 200) {
            this.certList = res.data.data
          } else {
            this.$message.error('')
          }
        })
        .catch(_error => {
          this.$message.error('')
        })
    },
    // 
    showAddCertDialog () {
      this.addCertDialogVisable = true
    },
    // 
    resetDialog () {
      this.$refs.addFormRef.resetFields()
    },
    // 
    addCert () {
      // 
      this.$refs.addFormRef.validate(valid => {
        console.log(this.addCertForm)
        if (valid === true) {
          // 
          this.$http.post('/user/add_cert', this.addCertForm)
            .then(res => {
              if (res.data.state === 200) {
                this.addCertDialogVisable = false
                this.$message.success('')
              } else {
                this.$message.error('')
              }
            })
            .catch(_error => {
              this.$message.error('' + _error)
            })
        }
      })
    },
    // 
    showModifyDialog (row) {
      console.log(row)
      this.modifyCertDialogVisable = true
      this.modifyCertForm.pre_id = row.id
      this.modifyCertForm.cert_no = row.cert_no
      this.modifyCertForm.cert_name = row.cert_name
      this.modifyCertForm.cert_date = row.cert_date
      // console.log(row)
    },
    // 
    resetModifyDialog () {
      this.$refs.modifyFormRef.resetFields()
      this.searchCert()
    },
    // 
    modifyCert () {
      // 
      this.$refs.modifyFormRef.validate(valid => {
        if (valid === true) {
          this.$http.post('/user/modify_user_cert', this.modifyCertForm)
            .then(res => {
              if (res.data.state === 200) {
                this.modifyCertDialogVisable = false
                this.$message.success('')
              } else {
                this.$message.error('')
              }
            })
            .catch(_error => {
              this.$message.error('')
            })
        }
      })
    }
  }
}
</script>
<style lang="less" scoped>
.el-table{
  margin-top: 10px;
}
.el-pagination{
  margin-top: 10px;
}
</style>
