<template>
  <div>
    <!--  -->
    <el-breadcrumb separator="/">
      <el-breadcrumb-item :to="{ path: '/home' }"></el-breadcrumb-item>
      <el-breadcrumb-item></el-breadcrumb-item>
      <el-breadcrumb-item></el-breadcrumb-item>
    </el-breadcrumb>
    <el-card>
      <el-row>
        <el-col :span="10">
          <div class="grid-content bg-purple-dark">
            <el-col :span="10" class="addBtn">
              <el-button type="primary" @click="showAddHolidayDialog()"></el-button>
            </el-col>
            <el-col :span="5" class="addBtn">
              <el-button type="info" @click="searchHoliday()"></el-button>
            </el-col>
          </div>
        </el-col>
      </el-row>
      <el-table :data="holidayList" border style="width: 100%">
        <el-table-column
          prop="bdate"
          label=""
          width="200">
        </el-table-column>
        <el-table-column
          prop="edate"
          label=""
          width="200">
        </el-table-column>
        <el-table-column
          prop="apply_date"
          label=""
          width="200">
        </el-table-column>
        <el-table-column
          prop="notes"
          label=""
          width="200">
        </el-table-column>
        <el-table-column
          prop="date_num"
          label=""
          width="100">
        </el-table-column>
        <el-table-column
          prop=""
          label="" >
          <template slot-scope="scope">
            <el-button type="success" @click="showModifyDialog(scope.row)" size="mini"></el-button>
          </template>
        </el-table-column>
      </el-table>
    </el-card>
    <!--  -->
    <el-dialog
      title="" :visible.sync="addHolidayDialogVisable" width="35%" @close="resetDialog">
      <el-form ref="addFormRef" :model="addHolidayForm" label-width="110px" :rules="addHolidayFormRules">
        <el-form-item label="" prop="dateRange">
          <el-date-picker
            v-model="addHolidayForm.dateRange"
            type="daterange"
            range-separator=""
            start-placeholder=""
            end-placeholder=""
            :picker-options="datePickerOptions">
          </el-date-picker>
        </el-form-item>
        <el-form-item label="" prop="notes">
          <el-input v-model="addHolidayForm.notes"></el-input>
        </el-form-item>
        <el-form-item >
              <span class="button_span">
                <el-button type="primary" @click="addHoliday()" size="small"></el-button>
                <el-button type="info" @click="resetDialog ()" size="small"></el-button>
              </span>
        </el-form-item>
      </el-form>
    </el-dialog>
    <!--  -->
    <el-dialog
      title="" :visible.sync="modifyHolidayDialogVisable" width="35%" @close="resetModifyDialog">
      <el-form ref="modifyFormRef" :model="modifyHolidayForm" label-width="110px" :rules="modifyHolidayFormRules">
        <el-form-item label="" prop="dateRange">
          <el-date-picker
            v-model="modifyHolidayForm.dateRange"
            type="daterange"
            range-separator=""
            start-placeholder=""
            end-placeholder=""
            :picker-options="datePickerOptions">
          </el-date-picker>
        </el-form-item>
        <el-form-item label="" prop="notes">
          <el-input v-model="modifyHolidayForm.notes"></el-input>
        </el-form-item>
        <el-form-item >
              <span class="button_span">
                <el-button type="primary" @click="modifyHoliday()" size="small"></el-button>
                <el-button type="info" @click="resetModifyDialog ()" size="small"></el-button>
              </span>
        </el-form-item>
      </el-form>
    </el-dialog>
  </div>
</template>
<script>
export default {
  data () {
    return {
      holidayList: [],
      addHolidayDialogVisable: false,
      datePickerOptions: {
        disabledDate: (time) => {
          const nowDate = new Date()
          const oneDay = 1000 * 60 * 60 * 24
          const oneYearLater = new Date(nowDate.getTime() + (oneDay * 365))
          return time.getTime() < nowDate || time.getTime() > oneYearLater
        }
      },
      addHolidayForm: {
        dateRange: '',
        apply_date: '',
        date_num: '',
        notes: '',
        bdate: '',
        edate: ''
      },
      addHolidayFormRules: {
        dateRange: [{ required: true, message: '', trigger: 'blur' }],
        notes: [{ required: true, message: '', trigger: 'blur' }]
      },
      modifyHolidayDialogVisable: false,
      modifyHolidayForm: {
        pre_id: '',
        apply_date: '',
        date_num: '',
        notes: '',
        bdate: '',
        edate: '',
        dateRange: ''
      },
      modifyHolidayFormRules: {
        dateRange: [{ required: true, message: '', trigger: 'blur' }],
        notes: [{ required: true, message: '', trigger: 'blur' }]
      }
    }
  },
  created () {
    this.searchHoliday()
  },
  methods: {
    // 
    searchHoliday () {
      this.$http.get('/user/get_user_holiday')
        .then(res => {
          if (res.data.state === 200) {
            console.log(res.data.data)
            this.holidayList = res.data.data
          } else {
            this.$message.error('')
          }
        })
        .catch(_error => {
          this.$message.error('')
        })
    },
    // 
    showAddHolidayDialog () {
      this.addHolidayDialogVisable = true
    },
    // 
    resetDialog () {
      this.$refs.addFormRef.resetFields()
    },
    // 
    addHoliday () {
      // 
      this.$refs.addFormRef.validate(valid => {
        console.log(this.addHolidayForm)
        if (valid === true) {
          this.addHolidayForm.bdate = this.addHolidayForm.dateRange[0]
          this.addHolidayForm.edate = this.addHolidayForm.dateRange[1]
          console.log(this.addHolidayForm)
          // 
          this.$http.post('/user/add_holiday', this.addHolidayForm)
            .then(res => {
              if (res.data.state === 200) {
                this.addHolidayDialogVisable = false
                this.$message.success('')
              } else {
                this.$message.error('')
              }
            })
            .catch(_error => {
              this.$message.error('' + _error)
            })
        }
      })
    },
    // 
    showModifyDialog (row) {
      console.log(row)
      this.modifyHolidayDialogVisable = true
      this.modifyHolidayForm.pre_id = row.id
      this.modifyHolidayForm.dateRange = row.dateRange
      this.modifyHolidayForm.bdate = row.bdate
      this.modifyHolidayForm.edate = row.edate
      this.modifyHolidayForm.notes = row.notes
      // console.log(row)
    },
    // 
    resetModifyDialog () {
      this.$refs.modifyFormRef.resetFields()
      this.searchHoliday()
    },
    // 
    modifyHoliday () {
      // 
      this.$refs.modifyFormRef.validate(valid => {
        if (valid === true) {
          this.modifyHolidayForm.bdate = this.modifyHolidayForm.dateRange[0]
          this.modifyHolidayForm.edate = this.modifyHolidayForm.dateRange[1]
          this.$http.post('/user/modify_user_holiday', this.modifyHolidayForm)
            .then(res => {
              if (res.data.state === 200) {
                this.modifyHolidayDialogVisable = false
                this.$message.success('')
                console.log('123' + this.modifyHolidayForm.bdate)
                console.log(this.modifyHolidayForm.edate)
              } else {
                this.$message.error('')
              }
            })
            .catch(_error => {
              this.$message.error('')
            })
        }
      })
    }
  }
}
</script>
<style lang="less" scoped>
.el-table{
  margin-top: 10px;
}
.el-pagination{
  margin-top: 10px;
}
</style>
