<template>
    <div>
        <!--  -->
        <el-breadcrumb separator="/">
            <el-breadcrumb-item :to="{ path: '/home' }"></el-breadcrumb-item>
            <el-breadcrumb-item></el-breadcrumb-item>
            <el-breadcrumb-item></el-breadcrumb-item>
        </el-breadcrumb>
        <el-row>
              <el-col :span="10">
                <el-form ref="modifyFormRef" :model="userForm" label-width="110px" :rules="userFormRules">
                    <el-form-item label="">
                    <el-input v-model="userForm.username" width="100px"  disabled></el-input>
                    </el-form-item>
                    <el-form-item label="" prop="address">
                        <el-input v-model="userForm.address"></el-input>
                    </el-form-item>
                    <el-form-item label="" prop="phone">
                        <el-input v-model="userForm.phone" ></el-input>
                    </el-form-item>
                    <el-form-item label="" prop="email">
                        <el-input v-model="userForm.email" ></el-input>
                    </el-form-item>
                    <el-form-item label="">
                        <el-select v-model="userForm.unit" placeholder="" disabled>
                            <el-option
                            v-for="item in unitSelect"
                            :key="item.id"
                            :label="item.name"
                            :value="item.id">
                            </el-option>
                        </el-select>
                        </el-form-item>
                        <el-form-item label="">
                        <el-select v-model="userForm.position" placeholder="" disabled>
                            <el-option
                            v-for="item in postSelect"
                            :key="item.id"
                            :label="item.name"
                            :value="item.id">
                            </el-option>
                        </el-select>
                    </el-form-item>
                    <el-form-item >
                    <span class="button_span">
                        <el-button type="primary" @click="modifyUser()" size="small"></el-button>
                    </span>
                    </el-form-item>
                </el-form>
              </el-col>
        </el-row>
    </div>
</template>
<script>
export default {
  data () {
    return {
      userForm: {
        username: '',
        address: '',
        phone: '',
        email: '',
        unit: '',
        position: ''
      },
      unitSelect: [],
      postSelect: [],
      userFormRules: {
        address: [{ required: true, message: '', trigger: 'blur' }],
        phone: [{ required: true, message: '', trigger: 'blur' }],
        email: [{ required: true, message: '', trigger: 'blur' }]
      }
    }
  },
  created () {
    this.getUnitSelect()
    this.getPostSelect()
    this.getUserInfo()
  },
  methods: {
    // 
    getUnitSelect () {
      this.$http.get('/user/getunitbyname?name=&current_index=0&page_size=1000')
        .then(res => {
          // console.log(res.data)
          if (res.data.state === 200) {
            this.unitSelect = res.data.data
          } else {
            this.$message.error('')
          }
        })
        .catch(_error => {
          // console.log(error)
          this.$message.error('')
        })
    },
    // 
    getPostSelect () {
      this.$http.get('/user/getpost?name=&current_index=0&page_size=1000')
        .then(res => {
          if (res.data.state === 200) {
            // console.log(res.data.data)
            this.postSelect = res.data.data
          } else {
            this.$message.error('')
          }
        })
        .catch(_error => {
          this.$message.error('')
        })
    },
    // 
    getUserInfo () {
      // 
      this.$http.get('/user/get_userinfo')
        .then(res => {
          console.log(res.data)
          if (res.data.state === 200) {
            this.userForm = res.data.data
          }
        })
        .catch(_error => {
          this.$message.error('' + _error)
        })
    },
    // 
    modifyUser () {
      this.$refs.modifyFormRef.validate(valid => {
        if (valid === true) {
          // 
          this.$http.put('/user/modify_userinfo', this.userForm)
            .then(res => {
              if (res.data.state === 200) {
                this.getUserInfo()
                this.$message.success('')
              } else {
                this.$message.error('')
              }
            })
            .catch(_error => {
              this.$message.error('')
            })
        } else {
          // this.getUserInfo()
        }
      })
    }
  }
}
</script>
