<template>
  <div>
    <!--  -->
    <el-breadcrumb separator="/">
      <el-breadcrumb-item :to="{ path: '/home' }"></el-breadcrumb-item>
      <el-breadcrumb-item></el-breadcrumb-item>
      <el-breadcrumb-item></el-breadcrumb-item>
    </el-breadcrumb>
    <el-card>
      <el-row>
        <el-col :span="10">
          <div class="grid-content bg-purple-dark">
            <el-col :span="5" class="addBtn">
              <el-button type="info" @click="searchHolidayFlow()"></el-button>
            </el-col>
          </div>
        </el-col>
      </el-row>
      <el-table :data="holidayFlowList" border style="width: 100%">
        <el-table-column
          prop="user_name"
          label=""
          width="100">
        </el-table-column>
        <el-table-column
          prop="type"
          label=""
          width="100">
          <template slot-scope="scope">
            <el-select v-model="scope.row.type" placeholder="" disabled>
              <el-option
                v-for="item in options"
                :key="item.value"
                :label="item.label"
                :value="item.value">
              </el-option>
            </el-select>
          </template>
        </el-table-column>
        <el-table-column
          prop="apply_date"
          label=""
          width="160">
        </el-table-column>
        <el-table-column
          prop="bdate"
          label=""
          width="160">
        </el-table-column>
        <el-table-column
          prop="edate"
          label=""
          width="160">
        </el-table-column>
        <el-table-column
          prop="notes"
          label=""
          width="160">
        </el-table-column>
        <el-table-column
          prop="date_num"
          label=""
          width="100">
        </el-table-column>
        <el-table-column
          prop=""
          label="">
          <template slot-scope="scope">
            <el-button type="success" @click="agree(scope.row)" size="mini"></el-button>
            <el-button type="info" @click="disagree(scope.row)" size="mini"></el-button>
          </template>
        </el-table-column>
      </el-table>
    </el-card>
  </div>
</template>
<script>
export default {
  data () {
    return {
      holidayFlowList: [],
      value: 0,
      options: [
        {
          value: 0,
          label: ''
        },
        {
          value: 1,
          label: ''
        }
      ]
    }
  },
  created () {
    this.searchHolidayFlow()
  },
  methods: {
    // 
    searchHolidayFlow () {
      this.$http.get('/admin/get_proving_holidayflow?username=&current_index=0&page_size=100')
        .then(res => {
          if (res.data.state === 200) {
            console.log(res.data.data)
            this.holidayFlowList = res.data.data
          } else {
            this.$message.error('')
          }
        })
        .catch(_error => {
          this.$message.error('')
        })
    },
    // 
    agree (row) {
      this.$http.put('/admin/deal_holiday_flow?id=' + row.id + '&type=0')
        .then(res => {
          if (res.data.state === 200) {
            this.searchHolidayFlow()
            this.$message.success('')
          } else {
            this.$message.error('1111')
          }
        })
        .catch(_error => {
          this.$message.error('' + _error)
        })
    },
    // 
    disagree (row) {
      this.$http.put('/admin/deal_holiday_flow?id=' + row.id + '&type=1')
        .then(res => {
          if (res.data.state === 200) {
            this.searchHolidayFlow()
            this.$message.success('')
          } else {
            this.$message.error('')
          }
        })
        .catch(_error => {
          this.$message.error('' + _error)
        })
    }
  }
}
</script>
<style lang="less" scoped>
.el-table{
  margin-top: 10px;
}
.el-pagination{
  margin-top: 10px;
}
.addBtn{
  margin-left: 10px;
}
</style>
