<template>
    <div>
        <!--  -->
        <el-breadcrumb separator="/">
            <el-breadcrumb-item :to="{ path: '/home' }"></el-breadcrumb-item>
            <el-breadcrumb-item></el-breadcrumb-item>
            <el-breadcrumb-item></el-breadcrumb-item>
        </el-breadcrumb>
        <el-card>
            <el-row>
              <el-col :span="10">
                <div class="grid-content bg-purple-dark">
                <el-input placeholder="" v-model="certName" @change="searchCert()">
                  <el-button slot="append" icon="el-icon-search" @click="searchCert()"></el-button>
                </el-input>
                </div>
              </el-col>
            </el-row>
            <el-table :data="certList" border style="width: 100%">
              <el-table-column
                prop="apply_date"
                label=""
                width="100">
              </el-table-column>
              <el-table-column
                prop="type"
                label=""
                width="100">
              </el-table-column>
              <el-table-column
                prop="cert_no"
                label=""
                width="180">
              </el-table-column>
              <el-table-column
                prop="cert_name"
                label="">
              </el-table-column>
              <el-table-column
                prop="cert_date"
                label="">
              </el-table-column>
              <el-table-column
                prop="approve_result"
                label="">
              </el-table-column>
              <el-table-column
                prop="state"
                label="">
              </el-table-column>
            </el-table>
            <el-pagination
              @size-change="handleSizeChange"
              @current-change="handleCurrentChange"
              :page-sizes="page_sizes"
              :page-size="page_size"
              layout="total, sizes, prev, pager, next"
              :total="total">
            </el-pagination>
        </el-card>
    </div>
</template>
<script>
export default {
  data () {
    return {
      certName: '',
      certList: [],
      page_sizes: [1, 2, 5, 10],
      page_size: 5,
      current_page: 1,
      total: 0
    }
  },
  created () {
    this.searchCert()
  },
  methods: {
    searchCert () {
      const currentIndex = (this.current_page - 1) * this.page_size
      this.$http.get('/user/get_user_certflow?cert_name=' + this.certName + '&current_index=' + currentIndex + '&page_size=' + this.page_size)
        .then(res => {
          if (res.data.state === 200) {
            console.log(res.data)
            this.certList = res.data.data
            for (var i = 0; i < this.certList.length; i++) {
              if (this.certList[i].type === 0) {
                this.certList[i].type = ''
              } else {
                this.certList[i].type = ''
              }

              //
              if (this.certList[i].approve_result === null) {
                this.certList[i].approve_result = ''
              } else if (this.certList[i].approve_result === 0) {
                this.certList[i].approve_result = ''
              } else if (this.certList[i].approve_result === 1) {
                this.certList[i].approve_result = ''
              }

              //
              if (this.certList[i].state === 0) {
                this.certList[i].state = ''
              } else if (this.certList[i].state === 1) {
                this.certList[i].state = ''
              }
            }
            this.total = res.data.total
          } else {
            this.$message.error('')
          }
        })
        .catch(_error => {
          this.$message.error('')
        })
    },
    // page_size
    handleSizeChange (pageSize) {
      this.page_size = pageSize
      this.searchCert()
    },
    // current_page
    handleCurrentChange (currentPage) {
      this.current_page = currentPage
      this.searchCert()
    }
  }
}
</script>
<style lang="less" scoped>
.el-table{
  margin-top: 10px;
}
.el-pagination{
  margin-top: 10px;
}
.addBtn{
  margin-left: 10px;
}
</style>
