<template>
    <div>
        <!--  -->
        <el-breadcrumb separator="/">
            <el-breadcrumb-item :to="{ path: '/home' }"></el-breadcrumb-item>
            <el-breadcrumb-item></el-breadcrumb-item>
            <el-breadcrumb-item></el-breadcrumb-item>
        </el-breadcrumb>
        <!--  -->
        <el-card>
            <el-row>
              <el-col :span="10">
                <div class="grid-content bg-purple-dark">
                <el-input placeholder="" v-model="userName" @change="searchUser()">
                  <el-button slot="append" icon="el-icon-search" @click="searchUser()"></el-button>
                </el-input>
                </div>
              </el-col>
              <el-col :span="5" class="addBtn">
                <el-button type="primary" @click="showAddUserDialog()"></el-button>
              </el-col>
            </el-row>
            <el-table :data="userList" border style="width: 100%">
              <el-table-column
                prop="username"
                label=""
                width="100">
              </el-table-column>
              <el-table-column
                prop="address"
                label=""
                width="180">
              </el-table-column>
              <el-table-column
                prop="phone"
                label="">
              </el-table-column>
              <el-table-column
                prop="email"
                label="">
              </el-table-column>
              <el-table-column
                prop="unit"
                label="">
              </el-table-column>
              <el-table-column
                prop="position"
                label="">
              </el-table-column>
              <el-table-column
                prop=""
                label="" width="180">
                <template slot-scope="scope">
                  <el-button type="danger" @click="deleteUser(scope.row)" size="mini"></el-button>
                  <el-button type="success" @click="showModifyDialog(scope.row)" size="mini"></el-button>
                </template>
              </el-table-column>
            </el-table>
            <el-pagination
              @size-change="handleSizeChange"
              @current-change="handleCurrentChange"
              :page-sizes="page_sizes"
              :page-size="page_size"
              layout="total, sizes, prev, pager, next"
              :total="unitListNum">
            </el-pagination>
        </el-card>
        <!--  -->
        <el-dialog
          title="" :visible.sync="addUserDialogVisable" width="35%" @close="resetDialog">
          <el-form ref="addFormRef" :model="addUserForm" label-width="110px" :rules="addUserFormRules">
            <el-form-item label="" prop="username">
              <el-input v-model="addUserForm.username"></el-input>
            </el-form-item>
            <el-form-item label="" prop="address">
              <el-input v-model="addUserForm.address"></el-input>
            </el-form-item>
            <el-form-item label="" prop="phone">
              <el-input v-model="addUserForm.phone"></el-input>
            </el-form-item>
            <el-form-item label="" prop="email">
              <el-input v-model="addUserForm.email"></el-input>
            </el-form-item>
            <el-form-item label="">
              <el-select v-model="unitSelected" placeholder="">
                <el-option
                  v-for="item in unitSelect"
                  :key="item.id"
                  :label="item.name"
                  :value="item.id">
                </el-option>
              </el-select>
            </el-form-item>
            <el-form-item label="">
              <el-select v-model="postSelected" placeholder="">
                <el-option
                  v-for="item in postSelect"
                  :key="item.id"
                  :label="item.name"
                  :value="item.id">
                </el-option>
              </el-select>
            </el-form-item>
            <el-form-item >
              <span class="button_span">
                <el-button type="primary" @click="addUser()" size="small"></el-button>
                <el-button type="info" @click="resetDialog ()" size="small"></el-button>
              </span>
            </el-form-item>
        </el-form>
        </el-dialog>
        <!--  -->
        <el-dialog
          title="" :visible.sync="modifyUserDialogVisable" width="35%" @close="resetModifyDialog">
          <el-form ref="modifyFormRef" :model="modifyUserForm" label-width="110px" :rules="modifyUserFormRules">
            <el-form-item label="" prop="username">
              <el-input v-model="modifyUserForm.username" disabled></el-input>
            </el-form-item>
            <el-form-item label="" prop="address">
              <el-input v-model="modifyUserForm.address"></el-input>
            </el-form-item>
            <el-form-item label="" prop="phone">
              <el-input v-model="modifyUserForm.phone"></el-input>
            </el-form-item>
            <el-form-item label="" prop="email">
              <el-input v-model="modifyUserForm.email"></el-input>
            </el-form-item>
            <el-form-item label="">
              <el-select v-model="modifyUnitSelected" placeholder="">
                <el-option
                  v-for="item in modifyUnitSelect"
                  :key="item.id"
                  :label="item.name"
                  :value="item.id">
                </el-option>
              </el-select>
            </el-form-item>
            <el-form-item label="">
              <el-select v-model="modifyPostSelected" placeholder="">
                <el-option
                  v-for="item in modifyPostSelect"
                  :key="item.id"
                  :label="item.name"
                  :value="item.id">
                </el-option>
              </el-select>
              </el-form-item>
              <el-form-item >
                <span class="button_span">
                  <el-button type="primary" @click="modifyUser()" size="small"></el-button>
                  <el-button type="info" @click="resetModifyDialog ()" size="small"></el-button>
                </span>
              </el-form-item>
          </el-form>
        </el-dialog>
    </div>
</template>
<script>
export default {
  data () {
    return {
      userName: '',
      userList: [],
      page_sizes: [1, 2, 5, 10],
      page_size: 5,
      current_page: 1,
      unitListNum: 0,
      addUserDialogVisable: false,
      addUserForm: {
        username: '',
        address: '',
        phone: '',
        email: '',
        unit: '',
        position: ''
      },
      addUserFormRules: {
        username: [{ required: true, message: '', trigger: 'blur' }],
        address: [{ required: true, message: '', trigger: 'blur' }],
        phone: [{ required: true, message: '', trigger: 'blur' }],
        email: [{ required: true, message: '', trigger: 'blur' }],
        unit: [{ required: true, message: '', trigger: 'blur' }],
        position: [{ required: true, message: '', trigger: 'blur' }]
      },
      unitSelect: [],
      unitSelected: '',
      postSelect: [],
      postSelected: '',
      modifyUserDialogVisable: false,
      modifyUserForm: {
        username: '',
        address: '',
        phone: '',
        email: '',
        unit: '',
        position: ''
      },
      modifyUserFormRules: {
        username: [{ required: true, message: '', trigger: 'blur' }],
        address: [{ required: true, message: '', trigger: 'blur' }],
        phone: [{ required: true, message: '', trigger: 'blur' }],
        email: [{ required: true, message: '', trigger: 'blur' }],
        unit: [{ required: true, message: '', trigger: 'blur' }],
        position: [{ required: true, message: '', trigger: 'blur' }]
      },
      modifyUnitSelect: [],
      modifyUnitSelected: '',
      modifyPostSelect: [],
      modifyPostSelected: ''
    }
  },
  created () {
    this.searchUser()
  },
  methods: {
    searchUser () {
      const currentIndex = (this.current_page - 1) * this.page_size
      this.$http.get('/admin/getusersbyusername?username=' + this.userName + '&current_index=' + currentIndex + '&page_size=' + this.page_size)
        .then(res => {
          if (res.data.state === 200) {
            // console.log(res.data.data)
            this.userList = res.data.data
            this.unitListNum = res.data.allnum
          }
        })
    },
    // page_size
    handleSizeChange (pageSize) {
      this.page_size = pageSize
      this.searchUser()
    },
    // 
    handleCurrentChange (currentPage) {
      this.current_page = currentPage
      this.searchUser()
    },
    // 
    showAddUserDialog () {
      this.addUserDialogVisable = true
      // 
      this.getUnitSelect(0)
      // 
      this.getPostSelect(0)
    },
    // 
    getUnitSelect (flag) {
      this.$http.get('/user/getunitbyname?name=&current_index=0&page_size=1000')
        .then(res => {
          // console.log(res.data)
          if (res.data.state === 200) {
            if (flag === 0) {
              this.unitSelect = res.data.data
            } else if (flag === 1) {
              this.modifyUnitSelect = res.data.data
            }
          } else {
            this.$message.error('')
          }
        })
        .catch(_error => {
          // console.log(error)
          this.$message.error('')
        })
    },
    // 
    getPostSelect (flag) {
      this.$http.get('/user/getpost?name=&current_index=0&page_size=1000')
        .then(res => {
          if (res.data.state === 200) {
            // console.log(res.data.data)
            if (flag === 0) {
              this.postSelect = res.data.data
            } else if (flag === 1) {
              this.modifyPostSelect = res.data.data
            }
          } else {
            this.$message.error('')
          }
        })
        .catch(_error => {
          this.$message.error('')
        })
    },
    // 
    deleteUser (row) {
      this.$confirm('')
        .then(res => {
          // console.log(row)
          this.$http.put('/admin/delete_user?username=' + row.username)
            .then(res => {
              if (res.data.state === 200) {
                // 
                this.searchUser()
                this.$message.success('')
              } else {
                this.$message.error('')
              }
            })
            .catch(_error => {
              this.$message.error('')
            })
        })
        .catch(_error => {
          this.$message.info('')
        })
    },
    // 
    resetDialog () {
      this.$refs.addFormRef.resetFields()
    },
    // 
    addUser () {
      // 
      this.$refs.addFormRef.validate(valid => {
        if (valid === true) {
          // 
          this.addUserForm.unit = this.unitSelected
          this.addUserForm.position = this.postSelected
          // console.log(this.addUserForm)
          this.$http.post('/admin/create_user', this.addUserForm)
            .then(res => {
              if (res.data.state === 200) {
              // 
                this.searchUser()
                this.addUserDialogVisable = false
                this.$message.success('')
              } else {
                // 
                this.$message.error(res.data.msg)
              }
            })
            .catch(_error => {
              this.$message.error('' + _error)
            })
        }
      })
    },
    // 
    showModifyDialog (row) {
      console.log(row)
      this.modifyUserDialogVisable = true
      this.modifyUserForm.username = row.username
      this.modifyUserForm.phone = row.phone
      this.modifyUserForm.address = row.address
      this.modifyUserForm.email = row.email
      this.getModifyPostId(row.position)
      this.getModifyUnitId(row.unit)
      // 
      this.getUnitSelect(1)
      // 
      this.getPostSelect(1)
    },
    // 
    resetModifyDialog () {
      this.$refs.modifyFormRef.resetFields()
      this.modifyPostSelected = ''
      this.modifyUnitSelected = ''
    },
    // id
    getModifyPostId (name) {
      this.$http.get('/user/getpost?name=' + name + '&current_index=0&page_size=1')
        .then(res => {
          if (res.data.data[0]) {
            this.modifyPostSelected = res.data.data[0].id
          }
        })
    },
    // id
    getModifyUnitId (name) {
      this.$http.get('/user/getunitbyname?name=' + name + '&current_index=0&page_size=1')
        .then(res => {
          if (res.data.data[0]) {
            this.modifyUnitSelected = res.data.data[0].id
          }
        })
    },
    // 
    modifyUser () {
      this.$refs.modifyFormRef.validate(valid => {
        if (valid === true) {
          // 
          this.modifyUserForm.unit = this.modifyUnitSelected
          this.modifyUserForm.position = this.modifyPostSelected
          // 
          this.$http.put('/user/modify_user', this.modifyUserForm)
            .then(res => {
              console.log(res.data)
              this.modifyUserDialogVisable = false
              this.searchUser()
              this.$message.success('')
            })
            .catch(error => {
              console.log(error)
            })
        }
      })
    }
  }
}
</script>
<style lang="less" scoped>
.el-table{
  margin-top: 10px;
}
.el-pagination{
  margin-top: 10px;
}
.addBtn{
  margin-left: 10px;
}
</style>
