<template>
    <div>
        <!--  -->
        <el-breadcrumb separator="/">
            <el-breadcrumb-item :to="{ path: '/home' }"></el-breadcrumb-item>
            <el-breadcrumb-item></el-breadcrumb-item>
            <el-breadcrumb-item></el-breadcrumb-item>
        </el-breadcrumb>
        <el-card>
            <el-row>
              <el-col :span="10">
                <div class="grid-content bg-purple-dark">
                <el-input placeholder="" v-model="postName" @change="searchPost()">
                  <el-button slot="append" icon="el-icon-search" @click="searchPost()"></el-button>
                </el-input>
                </div>
              </el-col>
              <el-col :span="5" class="addBtn">
                <el-button type="primary" @click="showAddPostDialog()"></el-button>
              </el-col>
            </el-row>
            <el-table :data="postList" border style="width: 100%">
              <el-table-column
                prop="name"
                label=""
                width="250">
              </el-table-column>
              <el-table-column
                prop="notes"
                label=""
                width="350">
              </el-table-column>
              <el-table-column
                prop=""
                label="">
                <template slot-scope="scope">
                  <el-button type="danger" @click="deletePost(scope.row)" size="mini"></el-button>
                  <el-button type="success" @click="showModifyDialog(scope.row)" size="mini"></el-button>
                </template>
              </el-table-column>
            </el-table>
            <el-pagination
              @size-change="handleSizeChange"
              @current-change="handleCurrentChange"
              :page-sizes="page_sizes"
              :page-size="page_size"
              layout="total, sizes, prev, pager, next"
              :total="postListNum">
            </el-pagination>
        </el-card>
        <!-- -->
        <el-dialog
          title="" :visible.sync="addPostDialogVisable" width="35%" @close="resetAddDialog">
          <el-form ref="addFormRef" :model="addPostForm" label-width="110px" :rules="addPostFormRules">
            <el-form-item label="" prop="name">
              <el-input v-model="addPostForm.name"></el-input>
            </el-form-item>
            <el-form-item label="" prop="notes">
              <el-input v-model="addPostForm.notes" ></el-input>
            </el-form-item>
            <el-form-item >
              <span class="button_span">
                <el-button type="primary" @click="addPost()" size="small"></el-button>
                <el-button type="info" @click="resetAddDialog ()" size="small"></el-button>
              </span>
            </el-form-item>
        </el-form>
        </el-dialog>
        <!-- -->
        <el-dialog
          title="" :visible.sync="modifyPostDialogVisable" width="35%" @close="resetModifyDialog">
          <el-form ref="modifyFormRef" :model="modifyPostForm" label-width="110px" :rules="modifyPostFormRules">
            <el-form-item label="" prop="name">
              <el-input v-model="modifyPostForm.name"></el-input>
            </el-form-item>
            <el-form-item label="" prop="notes">
              <el-input v-model="modifyPostForm.notes" ></el-input>
            </el-form-item>
            <el-form-item >
              <span class="button_span">
                <el-button type="primary" @click="modifyPost()" size="small"></el-button>
                <el-button type="info" @click="resetModifyDialog ()" size="small"></el-button>
              </span>
            </el-form-item>
        </el-form>
        </el-dialog>
    </div>
</template>
<script>
export default {
  data () {
    return {
      postName: '',
      postList: [],
      page_sizes: [1, 2, 5, 10],
      page_size: 5,
      postListNum: 0,
      current_page: 1,
      addPostDialogVisable: false,
      addPostForm: {
        name: '',
        notes: ''
      },
      addPostFormRules: {
        name: [{ required: true, message: '', trigger: 'blur' }],
        notes: [{ required: true, message: '', trigger: 'blur' }]
      },
      modifyPostDialogVisable: false,
      modifyPostForm: '',
      modifyPostFormRules: {
        name: [{ required: true, message: '', trigger: 'blur' }],
        notes: [{ required: true, message: '', trigger: 'blur' }]
      }
    }
  },
  created () {
    this.searchPost()
  },
  methods: {
    // 
    searchPost () {
      const currentIndex = (this.current_page - 1) * this.page_size
      this.$http.get('/user/getpost?name=' + this.postName + '&current_index=' + currentIndex + '&page_size=' + this.page_size)
        .then(res => {
          if (res.data.state === 200) {
            this.postList = res.data.data
            this.postListNum = res.data.total
          } else {
            this.$message.error('')
          }
        })
        .catch(_error => {
          this.$message.error('')
        })
    },
    // 
    deletePost (row) {
      this.$confirm('')
        .then(res => {
          // 
          this.$http.put('/admin/delte_post?id=' + row.id)
            .then(res => {
              this.searchPost()
              this.$message.success('')
            })
            .catch(_error => {
              this.$message.error('')
            })
        })
        .catch(_error => {
          this.$message.info('')
        })
    },
    showModifyDialog (row) {
      this.modifyPostDialogVisable = true
      this.modifyPostForm = row
    },
    // 
    handleSizeChange (pageSize) {
      this.page_size = pageSize
      this.searchPost()
    },
    // 
    handleCurrentChange (currentPage) {
      this.current_page = currentPage
      this.searchPost()
    },
    // 
    resetAddDialog () {
      this.$refs.addFormRef.resetFields()
      this.searchPost()
    },
    // 
    showAddPostDialog () {
      this.addPostDialogVisable = true
    },
    // 
    addPost () {
      // 
      this.$refs.addFormRef.validate(valid => {
        if (valid === true) {
          // 
          this.$http.post('/admin/create_post', this.addPostForm)
            .then(res => {
              if (res.data.state === 200) {
                this.addPostDialogVisable = false
                this.searchPost()
                this.$message.success('')
              } else {
                this.$message.error(res.data.msg)
              }
            })
            .catch(_error => {
              this.$message.error('')
            })
        }
      })
    },
    // 
    resetModifyDialog () {
      this.$refs.modifyFormRef.resetFields()
    },
    // 
    modifyPost () {
      // 
      this.$refs.modifyFormRef.validate(valid => {
        if (valid === true) {
          // 
          this.$http.put('/admin/modify_post', this.modifyPostForm)
            .then(res => {
              if (res.data.state === 200) {
                this.modifyPostDialogVisable = false
                this.searchPost()
                this.$message.success('')
              } else {
                this.$message.error('')
              }
            })
            .catch(_error => {
              this.$message.error('')
            })
        }
      })
    }
  }
}
</script>
<style scoped>
.el-table{
  margin-top: 10px;
}
.el-pagination{
  margin-top: 10px;
}
.addBtn{
  margin-left: 10px;
}
</style>
